:root{
    --secondary-color-theme:rgb(22,27,34);
    --main-color-theme:rgb(13,16,22);
    --dark-color-theme:rgb(0,5,9);
    --text-color-theme:#C9D1D9;
    --hover-color-theme:rgb(72, 200, 160);
}

*{
    box-sizing: border-box;
    transition: all 0.2s ease-in;
    font-family: Arial, Helvetica, sans-serif;
}

*::-webkit-scrollbar{
    display: none;
}




textarea:focus, input:focus{
    outline: none;
}

body{
    margin: 0;
    background: linear-gradient(109.6deg, rgb(72, 200, 160) 11.2%, rgb(32, 40, 48) 91.3%);
    width: 100vw;
    overflow: hidden;
}

#root,.chat{
height: 100dvh;
}


li{
    list-style: none;
    color: var(--text-color-theme);
}

p,h1,h2,label{
    color: var(--text-color-theme);
}

.messages > li{
    display: flex;
    margin: 0;
}

button{
    background-color: var(--dark-color-theme);
    color: var(--text-color-theme);
    border: none;
    border-radius: 5px;
}



button:hover{
    background-color: var(--hover-color-theme);
}

.messages > li > p{
    background-color: var(--hover-color-theme);
    border-radius: 5px;
    padding: 1%;
    margin: 2px;
    box-sizing: content-box;
    color: var(--dark-color-theme);
}

.li-to{
    justify-content: right;
}


h2{
    margin:0
}


.emoji-container{
    position: absolute;
    bottom: 6%;
    right: 1%;
}


.login-form,.register-form{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background-color: rgb(22 27 34 / 80%);
    border-radius: 10px;
    padding: 5%;
    box-shadow: 0 0 10px rgba(22 27 34 / 80);
    text-align: center;
}

.login-form > button,.register-form > button{
    margin-top: 10px;
    position: relative;
    left: 50%;
    transform: translateX(-100%);
    width: 50%;
    height: 30px;
}

.login-form > input,.register-form > input{
 border-bottom: 1px solid var(--hover-color-theme);
 border-radius: 0;
 background: transparent;
 margin-bottom: 10px;
}


button{
    cursor: pointer;
}


.contacts > ul > li:hover{
    background-color: var(--hover-color-theme);
    font-weight: bold;
}

.contacts > input{
    border-bottom: 1px solid var(--hover-color-theme);
    border-radius: 0;
    margin: 10px 0px 10px 0px;
    text-align: center;
}

.chat{
    display: flex;
}

.chat-list{
    height: 100%;
    width: 15vw;
    border-right: 1px solid black; 
    background-color: var(--main-color-theme);
}

.chat-messages{
    height: 100%;
    width: 100vw;

}


.contacts > ul > li{
    cursor: pointer;
    color: rgb(231,237,242);
    background-color: var(--secondary-color-theme);
    width: 100%;
    border-bottom: 1px solid var(--hover-color-theme);
    text-align: center;
    height: 50px;
    line-height: 50px;
}

.contacts{
    height: 80vh;
    text-align: center;
}

.chat-list-toggle{
    display: none;
    position: absolute;
    right: -40px;
    height: 5%;
    border-radius: 0;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: var(--secondary-color-theme);
    border: 1px solid black;
}

.user-profile{
    height: 20vh;
    text-align: center;
}

#chat-list-hide{
    width: 0vw !important;
}


.settings-container > button{
    width: 80%;
    height: 30px;
}

.contacts > ul{
    height: 70%;
    overflow-y: scroll;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

input{
    width: 100%;
    background-color: var(--main-color-theme);
    color: var(--text-color-theme);
    border: none;
    border-radius: 5px;
    padding: 1%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.other-people > ul{
    height: 75%;
    overflow-y: scroll;
}

.chat-list > h2{
    margin: 0;
}

.chat-list > ul > li{
    cursor: pointer;
}



.messages{
    height: 90%;
    margin: 0;
    border-bottom: 1px solid black;
    padding: 1%;
    background: linear-gradient(11deg, rgb(4 18 14) 11.2%, rgb(32, 40, 48) 91.3%);
    overflow-y: scroll;
}

.talking-to{
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    height: 5%;
    margin: 0;
    padding: 0;
    border-bottom: 1px solid black;
    text-align: center;
    background-color: var(--main-color-theme);
}

.talking-to > h1{
    margin: 0;
}

.talking-to > button{
    position: absolute;
    height: 4%;
    right:0;
    border-radius: 0;
    background:transparent;
}

button:hover > svg {
    fill: var(--hover-color-theme);
}

.send-message{
    height: 5%;
    display: flex;
    background-color: var(--main-color-theme);

}

.send-message > input{
    height: 100%;
    width: 75%;
    border: none;
}


.send-message-button{
    height: 100%;
    width: 25%;
    font-weight: bold;
    border:none;
    border-left: 1px solid black;
    cursor: pointer;
    border: 1px solid;
}

.emoji-picker{
    height: 100%;
    width: 10%;
    font-weight: bold;
    border:none;
    border-left: 1px solid black;
    cursor: pointer;
    border: 1px solid;
}


.delete-messages{
    width: 50%;
    height: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background-color: rgb(13 16 22 / 80%);
    border-radius: 10px;
    padding: 5%;
    box-shadow: 0 0 10px rgba(22 27 34 / 80);
    text-align: center;
}

.delete-messages > button{
    margin-top: 10px;
    position: relative;
    left: 50%;
    transform: translateX(-100%);
    width: 50%;
    height: 30px;
    margin-left: 1%;

}

.new-message{
    font-weight: bold;
    background-color: red !important;
}

.picker-container{
    position: absolute;
    bottom: 8%;
    right: 2%;
}
.auth-text{
    margin: 5px;
}

/* responsivnes */

/* Tablet */
@media only screen and (max-width: 1024px) {
    .login-form,.register-form,.delete-messages {
      width: 80%;
    }

    .chat-list{
        position: absolute;
        width: 30vw;
    }

    .chat-list-toggle{
        display: block;
    }
  }

/* Smartphone */

@media only screen and (max-width: 425px) {
    .login-form,.register-form,.delete-messages {
      width: 95%;
    }
    .chat-list{
        width: 42vw;
        
    }
  }

 


  /* Small Smartphone */

@media only screen and (max-width: 320px) {
    .picker-container{
        right: -5%;
    }

  }